<template>
  <div class="skills-radar">
    <canvas ref="canvas" aria-label="Skills Radar" role="img"></canvas>
  </div>
</template>

<script>
// v2 直接匯入 UMD
import Chart from "chart.js";

export default {
  name: "SkillsRadar",
  props: {
    // 例如：{ Figma:95, 'Design Tokens':85, HTML:100, CSS3:90, ... }
    skills: { type: Object, required: true },
    color: { type: String, default: "#FAD54D" },
    gridColor: { type: String, default: "rgba(255,255,255,0.12)" },
    textColor: { type: String, default: "#d9d9d9" }
  },
  data() {
    return { chart: null };
  },
  computed: {
    // ✅ 以 UI/UX 能力為 6 軸，對應多種常見欄位名稱（缺資料當 0）
    categories() {
      const s = this.skills || {};
      const get = k => (typeof s[k] === "number" && !isNaN(s[k]) ? s[k] : 0);
      const avg = keys => {
        const vals = keys.map(get);
        const sum = vals.reduce((a, b) => a + b, 0);
        return Math.round(sum / (vals.length || 1));
      };

      return [
        {
          label: "設計系統 / 元件化",
          value: avg([
            "Figma",
            "Design Tokens",
            "Component Library",
            "Auto Layout",
            "Variants",
            "Style Dictionary"
          ])
        },
        {
          label: "互動設計 / 原型驗證",
          value: avg([
            "Figma Prototype",
            "Axure RP",
            "Framer",
            "ProtoPie",
            "Principle",
            "User Flow",
            "Interaction Design"
          ])
        },
        {
          label: "研究與可用性",
          value: avg([
            "User Interview",
            "Usability Testing",
            "Heuristic",
            "A/B Testing",
            "Survey",
            "GA4",
            "Hotjar"
          ])
        },
        {
          label: "資訊架構 / 內容策略",
          value: avg([
            "IA",
            "Sitemaps",
            "Wireframing",
            "Content Design",
            "UX Writing"
          ])
        },
        {
          // 這軸會吃到你傳進來的 HTML / CSS3（例如 HTML:100, CSS3:90）
          label: "前端協作 / 可近用",
          value: avg(["HTML", "CSS3", "Accessibility", "WCAG", "ARIA"])
        },
        {
          label: "視覺敘事 / 品牌表現",
          value: avg([
            "Photoshop",
            "Illustrator",
            "Branding",
            "Motion",
            "After Effects"
          ])
        }
      ];
    },
    labels() {
      return this.categories.map(c => c.label);
    },
    dataValues() {
      return this.categories.map(c => c.value);
    }
  },
  mounted() {
    this.draw();
  },
  beforeDestroy() {
    if (this.chart) {
      this.chart.destroy();
      this.chart = null;
    }
  },
  methods: {
    draw() {
      const ctx = this.$refs.canvas.getContext("2d");
      if (this.chart) this.chart.destroy();

      this.chart = new Chart(ctx, {
        type: "radar",
        data: {
          labels: this.labels,
          datasets: [
            {
              label: "Skill Level",
              data: this.dataValues,
              backgroundColor: this.hexToRgba(this.color, 0.18),
              borderColor: this.color,
              pointBackgroundColor: this.color,
              pointBorderColor: this.color,
              borderWidth: 2,
              pointRadius: 3
            }
          ]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          legend: { display: false },
          tooltips: {
            callbacks: {
              label: function(tooltipItem, data) {
                const label = data.labels[tooltipItem.index];
                const value =
                  data.datasets[tooltipItem.datasetIndex].data[
                    tooltipItem.index
                  ];
                return label + ": " + value + "%";
              }
            }
          },
          // v2 的雷達用 scale
          scale: {
            ticks: {
              beginAtZero: true,
              min: 0,
              max: 100,
              stepSize: 20,
              fontColor: this.gridColor,
              backdropColor: "rgba(0,0,0,0)"
            },
            pointLabels: {
              fontColor: this.textColor,
              fontSize: 16
            },
            gridLines: {
              color: this.gridColor
            },
            angleLines: {
              color: this.gridColor
            }
          }
        }
      });
    },
    hexToRgba(hex, alpha) {
      const h = hex.replace("#", "");
      const r = parseInt(h.substring(0, 2), 16);
      const g = parseInt(h.substring(2, 4), 16);
      const b = parseInt(h.substring(4, 6), 16);
      return `rgba(${r},${g},${b},${alpha})`;
    }
  }
};
</script>

<style scoped>
.skills-radar {
  width: 100%;
  height: 600px;
  position: relative;
}
canvas {
  max-width: 100%;
  max-height: 600px;
}
</style>



